.home-extinct-sub-block-container {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}
.home-extinct-sub-block-wrap-upper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.home-extinct-sub-block-wrap-lower {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
.home-extinct-sub-block {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}
.home-extinct-img {
  width: 250px;
  height: 250px;
  border-radius: 35px;
  margin: 5px 1px;
}
#HomeExtinct hr {
  margin: 0;
}
#HomeExtinct h2 {
  color: white;
  font-weight: normal;
}
