#AboutMoti {
  width: 100%;
}
.ab-banner-moti {
  background: url(../../assets/about/banner_moti.jpg) no-repeat;
  background-size: 100% 100%;
  height: 54vh;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}
.ab-banner-moti h1 {
  color: white;
  font-size: 3.5rem;
}
.AboutMoti h2 {
  font-size: 2rem;
  text-align: center;
}
/*text*/
.ab-moti-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}
.ab-moti-p {
  width: 40vw;
  min-width: 650px;
}
.ab-moti-img-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.ab-moti-container img {
  width: 20vw;
  min-width: 300px;
  margin-bottom: 10px;
}
/*celebrity*/
.ab-moti-banner-celebrity {
  background-color: #edece4;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.ab-moti-content-celebrity {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: nowrap;
  width: 70vw;
  height: 30vh;
  min-width: 650px;
}
.ab-moti-content-celebrity-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
}
.ab-moti-content-celebrity-person {
  width: 12vw;
  min-width: 150px;
  text-align: center;
}
.ab-moti-content-celebrity-person img {
  width: 10vw;
  border-radius: 500px;
  margin-bottom: 10px;
}
.ab-moti-content-celebrity-person h3,
.ab-moti-content-celebrity-person p {
  margin: 0;
}
.ab-moti-content-celebrity-saying {
  width: 40vw;
  min-width: 500px;
  text-align: center;
}
