@font-face {
  font-family: LEDFont;
  src: url("../../assets/fonts/UnidreamLED.ttf");
}
.dynamic-num {
  font-family: LEDFont, sans-serif;
  font-size: 40px;
  display: inline-block;
  text-align: center;
}
.dynamic-num-orange {
  color: chocolate;
}
.dynamic-num-red {
  color: crimson;
}
#num1 {
  width: 140px;
}
#num2 {
  width: 110px;
}
#num3 {
  width: 120px;
  font-size: 40px;
  font-weight: normal;
}
#num4 {
  width: 60px;
  font-size: 40px;
  font-weight: normal;
}
#num5 {
  width: 90px;
  font-size: 40px;
  font-weight: normal;
}
.home-overview-stat-title {
  font-size: 24px;
  line-height: 30px;
}
.home-overview-text p {
  font-size: 20px;
  margin: 0;
  text-align: center;
  white-space: normal;
  word-break: break-word;
}
.home-overview-text span {
  font-size: 24px;
  font-weight: bold;
}
.home-overview-intro span {
  font-size: 24px;
  font-weight: bold;
  color: chocolate;
}
#num4-percentage {
  font-size: 30px;
  margin-right: 10px;
  color: chocolate;
}
.home-overview-intro {
  background-color: rgba(64, 87, 75, 0.87);
  width: 500px;
  height: 180px;
  padding: 20px;
  border-radius: 35px;
  box-shadow: -1px -1px 3px rgba(255, 255, 255, 0.1), 2px 2px 6px rgba(0, 0, 0, 0.2);
}
.home-overview-intro-au {
  width: 500px;
  height: 130px;
  margin-left: 1vw;
}
.home-overview-wrap {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}
.home-overview-wrap-left {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 66vh;
  width: 40%;
  z-index: 9999;
}
.home-overview-wrap-right {
  height: 57vh;
  width: 60%;
}
#home-overview-img {
  width: 100%;
}
#home-overview-webgl {
  outline: none;
  width: 50vw !important;
  height: 50vh !important;
}
#home-overview-webgl:hover {
  cursor: grab;
}
#home-overview-gui {
  position: absolute;
  top: 100px;
  left: 2px;
}
