#AboutUs {
  width: 100%;
}
/*video*/
.ab-banner-video {
  background-color: #465c51;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
.ab-content-video {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100vw;
}
.ab-content-video-container {
  width: 570px;
  margin: 75px 10px 60px 10px;
  max-height: 600px;
}
.ab-video {
  border-radius: 15px;
}
/*text*/
.ab-us-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
}
.ab-us-p-container {
  width: 38vw;
  min-width: 650px;
}
.ab-us-p-container span {
  font-weight: bold;
}
.AboutUs h2 {
  font-size: 2rem;
}
