#AboutFuture {
  width: 100%;
}
.ab-future-banner {
  background-color: #465c51;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  height: 40vh;
}
.ab-future-banner h1,
.ab-future-banner p,
.ab-future-banner h2 {
  color: white;
}
.ab-future-banner h1 {
  font-size: 2.2rem;
  margin: 0 0 5px 0;
}
.ab-future-banner p {
  font-size: 1.2rem;
  margin: 0 0 5px 0;
}
/*links*/
.ab-future-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  height: 26vh;
  width: 80vw;
}
.ab-future-content-wrap {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
}
.ab-future-content-img {
  color: white;
  width: 260px;
  height: 205px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
}
#ab-future-img-na {
  background: url(../../assets/about/future_na.jpg) no-repeat;
  background-size: cover;
}
#ab-future-img-sa {
  background: url(../../assets/about/future_sa.jpg) no-repeat;
  background-size: cover;
}
#ab-future-img-as {
  background: url(../../assets/about/future_as.jpg) no-repeat;
  background-size: cover;
}
#ab-future-img-af {
  background: url(../../assets/about/future_af.jpg) no-repeat;
  background-size: cover;
}
#ab-future-img-eu {
  background: url(../../assets/about/future_eu.jpg) no-repeat;
  background-size: cover;
}
.ab-future-banner h2 {
  font-size: 1.9rem;
  margin: 0;
  position: absolute;
  text-align: center;
}
.ab-future-content-hint {
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  width: 100%;
  height: 40px;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  line-height: 40px;
  margin-bottom: 0;
  margin-top: auto;
  visibility: hidden;
}
.ab-future-content-img:hover .ab-future-content-hint {
  visibility: visible;
  cursor: default;
}
@media only screen and (min-width: 1024px) and (max-width: 1365px) {
  .ab-future-banner h2 {
    width: 120px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .ab-future-banner h2 {
    width: 120px;
  }
}
